import {email, maxLength, minLength, numeric, required, requiredIf} from 'vuelidate/lib/validators'

export default {
  name: 'ContactUs',
  components: {},
  data () {
    return {
      inProgress: false,
      form: {
        name: '',
        subject: '',
        tel: '',
        email: '',
        msg: '',
        token: ''
      },
      show: true
    }
  },
  head () {
    return {
      title:  'Contact Us',
      meta: [
        {
          name: 'description',
          content: ''
        },
        {
          name: 'keywords',
          content: ''
        }
      ]
    }
  },
  validations: {
    form: {
      name: {
        required,
        serverError (value) {
          if (value === '') {
            return true
          }
          return !(this.errors != null && this.errors.username && value === this.errors.username.value)
        }
      },
      subject: {
        required,
        serverError (value) {
          if (value === '') {
            return true
          }
          return !(this.errors != null && this.errors.subject && value === this.errors.subject.value)
        }
      },
      email: {
        required,
        email,
        serverError (value) {
          if (value === '') {
            return true
          }
          return !(this.errors != null && this.errors.email && value === this.errors.email.value)
        }
      },
      msg: {
        required,
        serverError (value) {
          if (value === '') { return true }
          return !(this.errors != null && this.errors.msg && value === this.errors.msg.value)
        }
      }
    }
  },
  async mounted() {
    try {
      await this.$recaptcha.init()
    } catch (e) {
      console.log(e);
    }
  },
  methods: {
    async onSubmit(evt) {
      this.inProgress = true
      this.errors = null
      this.$v.$reset()
      evt.preventDefault()
      this.$v.form.$touch()
      if (this.$v.form.$invalid) {
        this.inProgress = false
        console.log('vuelidate error')
      } else {
        this.form.token = await this.$recaptcha.execute('contactus');

        this.$axios.post('message/create', this.form).then(() => {
          this.show = false
        }).catch((error) => {
          if (error.response.data.errors) {
            // console.log(JSON.stringify(error.response.data.errors))
            this.errors = error.response.data.errors
          }
          console.log(error)
        }).finally(() => {
          this.inProgress = false
        })
      }
    },
    onReset (evt) {
      this.errors = null
      this.$v.$reset()
      evt.preventDefault()
      // Reset our form values
      this.form.email = ''
      this.form.name = ''
      this.form.tel = ''
      this.form.subject = ''
      this.form.msg = ''
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    }
  }
  ,
  beforeDestroy() {
    this.$recaptcha.destroy()
  }
}
